.Home {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 100%;
  min-height: 600px;
  max-height: 800px;
  padding: 3rem 0 0;
  justify-content: center;
  align-items: center;
}
.Home-image {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 0 0 0;
}

.Home-image > img {
  object-fit: contain;
  width: 100%;
  border-radius: 100%;
  max-width: 160px;
}
.Home-info {
  width: 100%;
  text-align: center;
  padding: 0.5rem;
  border-radius: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.Home-info > header,
.Home-links,
.Home-resume {
  padding: 1.2rem 0.5rem;
}
.Home-info > header > p {
  font-size: 1.5rem;
  animation: left-right 1.2s ease-in-out;
}
.Home-info > header > h1 {
  animation: right-left 1.2s ease-in-out;
  font-size: 3rem;
  text-transform: uppercase;
  background: linear-gradient(
    90deg,
    rgba(254, 0, 103, 1) 0%,
    rgba(158, 84, 168, 1) 54%,
    rgba(68, 219, 250, 1) 100%
  );
  color: transparent;
  background-clip: text;
}
.Home-resume,
.Home-links {
  animation: bottom-top 1.2s ease-in-out;
}
.Home-resume > a {
  border: 2px solid rgb(103, 42, 174);
  padding: 0.5rem;
  margin: 1.2rem 0.5rem;
  border-radius: 20px;
  color: rgb(0, 0, 0);
}
.Home-resume > a:hover {
  border: 2px solid rgba(255, 0, 157, 0.438);
  padding: 0.5rem;
  margin: 1.2rem 0.5rem;
  border-radius: 20px;
  background-color: rgb(103, 42, 174);
  color: rgb(255, 255, 255);
}
.Home-links > a {
  border: 1px solid rgb(0, 185, 145);
  font-size: 25px;
  padding: 4px 8px 0;
  margin: 1.2rem 0.5rem;
  border-radius: 50%;
  display: flexbox;
}
.Home-links > a:hover {
  font-size: 20px;
  padding: 6px 10px 3px;
  border: 1px solid rgb(255, 0, 0);
}
@media screen and (min-width: 768px) {
  .Home {
    min-height: 98vh;
  }
}
@media screen and (min-width: 992px) {
  .Home {
    flex-direction: row;
    padding: 0;
  }
  .Home-image,
  .Home-info {
    width: 50%;
    min-height: 100vh;
  }
  .Home-image {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem 0 0 0;
  }
  .Home-image > img {
    object-fit: contain;
    border-radius: 100%;
    max-width: 250px;
  }
}
