.Skills {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 100%;
  min-height: 550px;
  align-items: center;
  justify-content: center;
}
.Skills-header {
  width: 100%;
  font-size: 1.5rem;
  text-align: center;
}
.Skills-cards {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 1rem;
  margin: 0 auto;
}
.Skills-card h3 {
  text-align: center;
  padding: 0 0 1rem 0;
  font-size: 1.5rem;
}
.Skills-card {
  background-color: white;
  border: 2px solid white;
  border-radius: 10% 0 10% 0;
  min-height: 300px;
  height: 100%;
  margin: 1rem 0;
  box-shadow: 2px 2px 10px rgb(184, 184, 184);
  position: relative;
  overflow: hidden;
}
.Skills-card::before,
.Skills-card::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: -50%;
  left: -50%;
  background: linear-gradient(45deg, transparent, #0ff0fc);
  animation: animate 6s linear infinite;
  transform-origin: bottom right;
}
.Skills-card::after {
  background: linear-gradient(45deg, transparent, #ff0095);
  animation-delay: -3s;
}
.Skills-anime {
  inset: 3px;
  z-index: 11;
  background-color: rgb(236, 236, 236);
  position: absolute;
  border-radius: 10% 0 10% 0;
  padding: 3rem;
}
.Skills-anime:hover {
  background-color: rgb(255, 255, 255);
  transition: all 0.2s ease-in-out;
}
@keyframes animate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.Skills-cards-list {
  display: flex;
  justify-content: space-around;
}
.Skills-cards-list p {
  padding: 0.6rem 0;
  font-size: 1.3rem;
  position: relative;
  cursor: pointer;
}
.Skills-cards-list p::after {
  content: "";
  position: absolute;
  width: 100%;
  background-color: rgb(255, 0, 153);
  bottom: -5px;
  left: -3px;
  height: 2px;
  transition: 0.4s ease-in-out;
  transform: scaleX(0);
}
.Skills-cards-list p:hover::after {
  transform: scaleX(1);
}
@media screen and (min-width: 992px) {
  .Skills-cards {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 1rem;
  }
  .Skills-card {
    max-width: 50%;
    width: 40%;
    padding: 1rem;
  }
  .Skills {
    padding: 6rem 0 0;
  }
}
