nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10000;
  max-width: 1440px;
}
nav > div {
  background-color: rgb(255, 255, 255);
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0.5rem 2rem;
  width: 100%;
  position: relative;
}
.Nav-logo {
  font-weight: 900;
  font-size: x-large;
  animation: left-right 1.5s ease-in-out;
}
.Nav-logo > span {
  color: red;
}
.Nav-humberger > span {
  width: 25px;
  height: 3px;
  padding: 1px;
  margin: 4px;
  background-color: black;
  display: block;
}
.Nav-humberger {
  background-color: white;
  animation: top-bottom 1s ease-out;
}
.Nav-humberger:hover {
  transform: scale(1.05);
  cursor: pointer;
}
.Nav-links {
  position: fixed;
  top: 51px;
  right: 0;
  width: 99%;
  background: linear-gradient(45deg, rgb(1, 1, 119), rgba(0, 60, 11, 0.585));
  padding: 3rem;
  transform: translateX(105%);
  transition: transform 0.7s ease-in-out;
}
.display {
  transform: translateX(0%);
}
.Nav-links > a {
  display: block;
  padding: 1rem;
  text-align: center;
  font-size: 1.2rem;
  color: white;
  position: relative;
}

.Nav-links > a:hover {
  color: rgb(255, 247, 0);
}
@media (min-width: 768px) {
  .Nav-humberger {
    display: none;
  }
  .Nav-links {
    position: static;
    display: flex;
    padding: 0;
    width: auto;
    background: white;
    transform: translateX(0%);
  }
  .Nav-links > a {
    color: rgb(0, 0, 0);
    padding: 0 1rem;
    animation: top-bottom 1.5s ease-out;
  }
  .Nav-links > a:hover {
    color: rgb(255, 0, 0);
    text-decoration: underline;
    text-underline-offset: 10px;
    transition: all .3s ease-in-out;
  }
}
