.Contact {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-height: 550px;
  margin: 0 auto;
}
.Contact-header {
  text-align: center;
  font-size: 1.3rem;
  padding: 3rem 0;
}
.Contact-cards {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.Contact-card {
  background-color: rgb(255, 249, 249);
  width: 90%;
  border: 2px solid rgb(255, 0, 102);
  border-radius: 0 50px;
  padding: 3rem 5rem;
  margin: 0 0 1rem 0;
  text-align: center;
  box-shadow: 2px 2px 10px rgb(255, 31, 176);
}
.Contact-card:hover{
  background-color: rgb(255, 255, 255);
}
.Contact-card > :first-child {
  padding: 0 0 1rem 0;
  position: relative;
}
.Contact-card > :first-child::after {
  content: "";
  width: 100%;
  background-color: rgb(2, 255, 52);
  position: absolute;
  bottom: 10px;
  left: 0;
  height: 2px;
  transition: 0.4s;
  transform: scaleX(0);
}
.Contact-card > :first-child:hover::after {
  transform: scaleX(1.2);
}
@media screen and (min-width: 576px) {
  .Contact-cards {
    flex-wrap: wrap;
    flex-direction: row;
  }
  .Contact-card {
    width: auto;
    margin: 0 1rem 1rem;
  }
}
@media screen and (min-width: 992px) {
  .Contact-cards {
    flex-wrap: nowrap;
    flex-direction: row;
  }
  .Contact-card {
    margin: 0 1rem 1rem;
  }
}
