.Works {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 100%;
  justify-content: center;
  align-items: center;
  padding: 5rem 0 0;
}
.Works>header{
  font-size: 1.5rem;
  text-align: center;
}
.Works-cards {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem 0;
  flex-direction: column;
  margin: 0 auto;
}
.Works-card {
  width: 80%;
  border: 2px solid rgb(0, 255, 251);
  border-radius: 50px 0;
  box-shadow: 2px 10px 10px rgba(0, 238, 255, 0.094);
  padding: 2.5rem;
  margin: 0 auto 1rem auto;
  text-align: center;
  background-color: rgb(255, 255, 255);
}
.Works-card:hover{
  background-color: rgb(224, 244, 246);
}
.Works-card > img {
  max-width: 180px;
  height: 130px;
  margin: 0 auto;
  padding: 0 0 15px 0;
}
.Works-card-content h2 {
  padding: 2rem;
  max-width: 100%;
}
.Works-card-content > div {
  display: flex;
  flex-direction: column;
  
}
.Works-card-content a {
  border: 2px solid rgb(103, 42, 174);
  padding: 0.5rem;
  margin: 1.2rem 0.5rem;
  border-radius: 20px;
  color: rgb(0, 0, 0);
  text-align: center;
}
.Works-card-content a:hover {
  border: 2px solid rgb(103, 42, 174);
  padding: 0.5rem;
  margin: 1.2rem 0.5rem;
  border-radius: 20px;
  background-color: rgb(103, 42, 174);
  color: rgb(255, 255, 255);
}
@media screen and (min-width: 576px) {
  .Works-cards {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .Works-card {
    margin: 0 1.4rem 1rem 0;
    width: auto;
   

  }
  .Works-card-content > div {
    display: inline;
  }
}
@media screen and (min-width: 992px) {
  .Works-cards {
    flex-direction: row;
    flex-wrap: nowrap;

  }
}
